@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin constent-padding-inline() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    padding-inline: 120px;
    max-width: 1260px;
    margin-inline: auto;
  }
}

.container {
  @include constent-padding-inline;
}

@mixin page-grid {
  --columns: 2;

    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(var(--columns), 1fr);


    @include on-tablet {
      --columns: 6;

      column-gap: 24px;
    }

    @include on-desktop {
      --columns: 12;
    }
  }

